import axios from "axios";
import { GET_CHALLENGES, GET_ERRORS, SET_LOADING_CHALLENGES } from "./types";

// Get all challenges
export const getChallenges = () => dispatch => {
  dispatch(setLoading());
  axios
    .get("/api/challenges")
    .then(res => {
      dispatch({
        type: GET_CHALLENGES,
        payload: res.data
      });
    })
    .catch(err =>
      dispatch({
        type: GET_ERRORS,
        payload: err.response.data
      })
    );
};

const setLoading = () => {
  return {
    type: SET_LOADING_CHALLENGES
  };
};
