import { GET_CHALLENGES, SET_LOADING_CHALLENGES } from "../actions/types";

const initialState = {
  challenge: {},
  challenges: [],
  isLoading: false
};

export default function(state = initialState, action) {
  switch (action.type) {
    case SET_LOADING_CHALLENGES:
      return {
        ...state,
        isLoading: true
      };
    case GET_CHALLENGES:
      return {
        ...state,
        challenges: action.payload,
        isLoading: false
      };
    default:
      return state;
  }
}
